import React, {ChangeEvent, DetailedHTMLProps, InputHTMLAttributes, useState} from 'react'
import s from  "../../styles.module.css";

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperRangePropsType = DefaultInputPropsType & { //  +       
    onChangeRange?: (value: number) => void
    valueMin: number
};

const SuperRange: React.FC<SuperRangePropsType> = (
    {
        type, //          
        onChange, onChangeRange,
        className, valueMin,

        ...restProps//       restProps
    }
) => {
    const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
        onChange && onChange(e) //   

        onChangeRange && onChangeRange(+e.currentTarget.value)
    }

    const finalRangeClassName = `${s.range} ${className ? className : ''}`
    const [value2, setValue2] = useState<number>(100)
    return (
        <>
            <div className={s.wrapperHW11}>
                <div className={s.priceInput}>
                    <div className={s.field}>
                        <input
                            type="number"
                            className={s.inputMin}
                            value={valueMin}
                            onChange={onChangeCallback}
                        />
                    </div>
                    <div className={s.field}>
                        <input
                            type="number"
                            className={s.inputMin}
                            value={value2}
                            onChange={event => ('')}
                        />
                    </div>
                </div>
                <div className={s.slider}>
                    <div className={s.progress} style={{left: `${0  + '%'}`, right: `${100 - valueMin + '%'}`}}></div>
                </div>
                <div className={s.rangeInput}>
                    <input
                        type="range"
                        className={s.rangeMin}
                        min={0}
                        max={100}
                        value={valueMin}
                        onChange={onChangeCallback}

                        {...restProps}
                    />
                    <input
                        type="range"
                        className={s.rangeMax}
                        min={0}
                        max={100}
                        value={value2}
                        onChange={event => ('')}

                        {...restProps}
                    />
                </div>
            </div>
        </>
    )
}

export default SuperRange
